import React from "react";
import L from "leaflet";
import Gps from "leaflet-gps";
import { getDistance } from "geolib";
import { Row, Col, Form, Button } from "react-bootstrap";

let map;
let marker;
let answers = [];
class Map extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      marker: [58.592003, 16.188313],
      questions: [
        {
          question: "Fråga 1: hur många kvitton har ni lämnat in?",
          alternatives: ["1:  20", "X: 30", "2: 40 "],
          coords: [[58.589957, 16.193378]],
        },
        {
          question: "Fråga 2: Är Anna snäll?",
          alternatives: ["1:  Alltid", "X: Ibland", "2: Nope "],
          coords: [
            [58.590729, 16.1925],
            [58.590729, 16.1925],
            [58.589852, 16.191838],
          ],
        },
        {
          question: "Fråga 3: Är Anna snäll?",
          alternatives: ["1:  Alltid", "X: Ibland", "2: Nope "],
          coords: [
            [58.590729, 16.1925],
            [58.590729, 16.1925],
            [58.589852, 16.191838],
          ],
        },
        {
          question: "Fråga 4: Är Anna snäll?",
          alternatives: ["1:  Alltid", "X: Ibland", "2: Nope "],
          coords: [
            [58.590729, 16.1925],
            [58.590729, 16.1925],
            [58.589852, 16.191838],
          ],
        },
      ],
      cur_q: 0,
      close: true,
      cur_selected: null,
      correct: [0, 0, 0, 0, 0, 0, 0],
    };
  }
  componentDidMount() {
    map = L.map("map", {
      center: [58.592003, 16.188313],
      zoom: 14,
      layers: [
        L.tileLayer(
          "https://{s}.basemaps.cartocdn.com/rastertiles/voyager_labels_under/{z}/{x}/{y}.png",
          {
            attribution: "Antique",
          }
        ),
      ],
    });
    marker = L.marker(this.state.questions[this.state.cur_q].coords[0]).addTo(map);

    var gps = new L.Control.Gps({
      autoActive: true,
      autoCenter: false,
    }); //inizialize control

    gps
      .on("gps:located", (e) => {
        //	e.marker.bindPopup(e.latlng.toString()).openPopup()
        console.log(e.latlng, map.getCenter());

        this.update(e);
      })
      .on("gps:disabled", (e) => {
        e.marker.closePopup();
      });

    gps.addTo(map);
    gps.activate();
  }

  render() {
    return (
      <Col>
        <Row>
          <div id="map" style={{ width: "100vw", height: "100vw" }}></div>
        </Row>
        <Row className="p-2">
          <Col sm={12}>
            <h2 id="coords"></h2>
            {!this.state.cur_q < this.state.questions.length && <h1 id="infotext"></h1>}
          </Col>

          {!this.state.close && <h1>Gå till nästa fråga</h1>}
          {this.state.close && this.state.cur_q < this.state.questions.length && (
            <Col>
              <h1>{this.state.questions[this.state.cur_q].question}</h1>

              <h3>
                <Form onSubmit={this.mySubmitHandler}>
                  <fieldset>
                    <Form.Group as={Row}>
                      <Col sm={10}>
                        <Form.Check
                          type="radio"
                          label={this.state.questions[this.state.cur_q].alternatives[0]}
                          name="Radios"
                          id="Radios1"
                          value={0}
                          onChange={this.onValueChange}
                        />
                        <Form.Check
                          type="radio"
                          label={this.state.questions[this.state.cur_q].alternatives[1]}
                          name="Radios"
                          id="Radios2"
                          value={1}
                          onChange={this.onValueChange}
                        />
                        <Form.Check
                          type="radio"
                          label={this.state.questions[this.state.cur_q].alternatives[2]}
                          name="Radios"
                          id="Radios3"
                          value={2}
                          onChange={this.onValueChange}
                        />
                      </Col>
                    </Form.Group>
                  </fieldset>

                  {this.state.cur_selected && (
                    <Form.Group as={Row}>
                      <Col sm={{ span: 10, offset: 2 }}>
                        <Button type="submit">Nästa</Button>
                      </Col>
                    </Form.Group>
                  )}
                </Form>
              </h3>
            </Col>
          )}
        </Row>
      </Col>
    );
  }

  mySubmitHandler = (event) => {
    event.preventDefault();

    answers.push(this.state.cur_selected);
    let nextQuestion = this.state.cur_q + 1;
    this.setState({ cur_q: nextQuestion });

    if (nextQuestion === this.state.questions.length) {
      let counter = 0;
      answers.forEach((element, i) => {
        if (parseInt(element) === this.state.correct[i]) {
          ++counter;
        }
      });
      document.getElementById("infotext").innerHTML = counter;
    } else {
      marker.setLatLng(
        this.state.questions[nextQuestion].coords[this.state.cur_selected]
      );
      this.setState({
        cur_selected: null,
      });
      this.setState({ close: false });
    }
  };

  onValueChange = (event) => {
    this.setState({
      cur_selected: event.target.value,
    });
  };

  update = (e) => {
    let distance = getDistance(
      { latitude: marker._latlng.lat, longitude: marker._latlng.lng },
      { latitude: e.latlng.lat, longitude: e.latlng.lng }
    );
    document.getElementById("coords").innerHTML = distance;

    this.setState({ close: distance < 30 });
  };
}

export default Map;
